.wrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #5471b3;
	width: 100%;
	margin: auto;
}

.task-name {
	color: rgba(255, 255, 255, 0.4);
	font-size: 1.2rem;
	padding: 20px;
}

.current-sentence {
	color: #fff;
	font-size: 1.875rem;
	padding: 10px 20px 20px;
}

.sentence-tiles {
	border-radius: 4px;
	box-shadow: 0 2px 2px 0 rgb(0 0 0 / 13%);
	color: #333;
	background: #fff;
	font-weight: 600;
	font-size: 1.5rem;
	user-select: none;
    min-width: 600px;
    width: 650px;
    max-width: 90%;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
}

.sentence-tiles > li {
	width: calc(100% / 3);
	cursor: pointer;
	border: 1px solid #E6E9EE;
	height: 90px;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: auto;
}

@media screen and (max-width: 420px) {
	.sentence-tiles {
		min-width: 320px;
		width: 100%;
	}
}

@media screen and (max-width: 640px) {
	.sentence-tiles {
		min-width: 320px;
		width: 90%;
	}
}

.show-rule {
	border: solid 1px rgba(255, 255, 255, 0.2);
	color: rgba(255, 255, 255, 0.4);
	font-size: .875rem;
	text-transform: uppercase;
	cursor: pointer;
	transition: all .2s ease;
	width: 192px;
    height: 46px;
    border-radius: 4px;
    background: transparent;
    margin: 20px;
}

.show-rule:hover {
	border-color: #fff;
	color: #fff;
}

.progress-bar-holder {
	height: 80px;
	background-color: #4b66a1;
	flex-grow: 1;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
}

.progress-bar {
	text-align: center;
	position: relative;
}

.progress-dot {
	display: inline-block;
	width: 16px;
	height: 16px;
	margin-right: 24px;
	border-radius: 50%;
	background: #435b91;
	z-index: 2;
}

.progress-dot:last-child {
	margin-right: 0;
}

.progress-dot.-current {
	position: relative;
}

.progress-dot.-current::after {
	content: '';
    display: block;
    height: 6px;
    width: 6px;
    border-radius: 50%;
    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.progress-bar::after {
	content: '';
    display: block;
    height: 6px;
    width: calc(100% - 12px);
    background-color: #435b91;
    position: absolute;
    left: 6px;
    top: calc(50% - 4px);
    transform: translateY(-50%);
}

.answer {
	font-size: 2rem;
	color: #fff;
	min-height: 50px;
}

@media screen and (max-width: 640px) {
	.answer {
		font-size: 2.4rem;
	}
}

.voice-over-icon {
    width: 40px;
    height: 40px;
    display: inline-block;
    background: url(../../../../static_assets/images/voice-over.svg) center no-repeat;
    vertical-align: middle;
    margin-right: 25px;
    cursor: pointer;
}

.answer > .-incorrect {
	text-decoration: line-through red;
	color: #ccc;
}

.delete-word {
	display: inline-block;
	vertical-align: middle;
	cursor: pointer;
	background: url(data:image/svg+xml;base64,);
	height: 20px;
    min-height: 20px;
    width: 26px;
    min-width: 26px;
    margin-left: 24px;
}

.-hidden {
	display: none !important;
}